<template>
  <div class="card">
    <div class="card-body">
      <div class="row">
        <div class="col-xl-12 col-lg-12">
          <div>
            <a-tabs defaultActiveKey="1">
              <a-tab-pane tab="Customer Data" key="1">
                <button @click="showModal" class="btn btn-primary pull-right mb-3 bringToTop">Print Shipment Data</button>
                <a-table
                  :columns="bestsellersColumns"
                  :dataSource="usersData"
                  class="utils__scrollTable"
                  :scroll="{ x: '100%' }"
                >
                  <a slot="shipping_mark" slot-scope="text" href="javascript: void(0);">{{text.name}}</a>
                  <span slot="phone" slot-scope="text">{{text}}</span>
                  <span slot="usd" slot-scope="text">${{text}}</span>
                  <span slot="naira" slot-scope="text">â‚¦{{text}}</span>
                  <button @click="deleteShipment(text.url)" class="btn btn-info" slot="action" slot-scope="text" >{{text.name}}</button>
                </a-table>
              </a-tab-pane>
            </a-tabs>
          </div>
        </div>
      </div>
    </div>

    <a-modal v-model="visible" title="" width="1200px" style="height: 100vh" onOk="handleOk">
      <template slot="footer">
        <a-button key="back" @click="handleCancel">Return</a-button>
        <a-button key="submit" type="primary" @click="handleOk">
          Print
        </a-button>
      </template>

      <div class="A4" >
        <div id="printDocument" class="sheet padding-10mm">
          <div class="row">
            <div class="col-md-12">
              <img src="/logos/Fred_Mac.png" class="mb-5" width="300">
              <br>
            </div>
          </div>

          <div class="ttable">
            <table>
              <tr>
                <th>Shipment Mark</th>
                <th>P/N</th>
                <th>CTN</th>
                <th>CBM</th>
                <th>TOTAL USD</th>
                <th>TOTAL NGN</th>
                <th>Container No</th>
              </tr>
              <tr v-for="(user, index) in modalData" :key="index">
                <td>{{ user.shipping_mark.name }}</td>
                <td>{{ user.phone }}</td>
                <td>{{ user.ctn }}</td>
                <td>{{ user.cbm }}</td>
                <td>{{ user.usd }}</td>
                <td>{{ user.naira }}</td>
                <td>{{ user.container }}</td>
              </tr>
            </table>
          </div>

        </div>
      </div>

    </a-modal>

  </div>
</template>

<script>

import Printd from 'printd'

const bestsellersColumns = [
  {
    title: 'ID',
    dataIndex: 'id',
    key: 'id',
    sorter: (a, b) => a.id - b.id,
  },
  {
    title: 'Shipment Mark',
    dataIndex: 'shipping_mark',
    key: 'shipping_mark',
    scopedSlots: { customRender: 'shipping_mark' },
  },
  {
    title: 'P/N',
    dataIndex: 'phone',
    key: 'phone',
    scopedSlots: { customRender: 'phone' },
    sorter: (a, b) => a.phone - b.phone,
  },
  {
    title: 'CTN',
    dataIndex: 'ctn',
    key: 'ctn',
  },
  {
    title: 'CBM',
    dataIndex: 'cbm',
    key: 'cbm',
  },
  {
    title: 'TOTAL USD',
    dataIndex: 'usd',
    key: 'usd',
    scopedSlots: { customRender: 'usd' },
  },
  {
    title: 'TOTAL IN NAIRA',
    dataIndex: 'naira',
    key: 'naira',
    scopedSlots: { customRender: 'naira' },
  },
  {
    title: 'Container No',
    dataIndex: 'container',
    key: 'container',
  },
  {
    title: 'Action',
    dataIndex: 'action',
    key: 'action',
    scopedSlots: { customRender: 'action' },
  },
]

export default {
  name: 'index',
  data: function () {
    return {
      bestsellersColumns,
      usersData: [],
      visible: false,
      modalData: [],
    }
  },
  mounted() {
    this.getAllCustomers()
  },
  methods: {
    showModal() {
      this.visible = true
      this.modalData = this.usersData
    },
    handleOk(e) {
      this.print()
    },
    handleCancel(e) {
      this.visible = false
    },
    print() {
      const d = new Printd()
      const cssText = `
        table {
          font-family: arial, sans-serif;
          border-collapse: collapse;
          width: 100%;
          margin-top: 20px;
        }

        td, th {
          border: 1px solid #dddddd;
          text-align: left;
          padding: 8px;
        }

        th {
          padding: 20px;
          background-color: #fafafa;
          border-top-left-radius: 3px;
          border-top-right-radius: 3px;
        }

        tr:nth-child(even) {
          background-color: #dddddd;
        }
      `
      d.print(document.getElementById('printDocument'), [cssText])
    },
    deleteShipment(id) {
      this.$nprogress.start()
      this.axios.post(`shipment/destroy/${id}`)
        .then(response => {
          this.$nprogress.done()
          // console.log(response)
          if (response.status === 200) {
            this.$notification['success']({
              message: 'Shipment Data',
              description: 'You have successfully Deleted a Shipment ',
            })
            const data = this.usersData
            const query = id
            // search through the array and get the index array with the passed value
            const index = data.map(function (img) {
              return img.id
            }).indexOf(query)
            // filter through the data, remove the value we don't want (with index) and return the values we need
            const filteredItems = data.slice(0, index).concat(data.slice(index + 1, data.length))
            // replace the data with the returned value
            this.usersData = filteredItems
          }
        })
        .catch(error => {
          // console.log(error)
          this.$notification['warning']({
            message: 'Customer Data',
            description: 'Sorry an error occurred',
          })
          return error
        })
    },
    getAllCustomers() {
      this.axios.get('shipment/all')
        .then(response => {
          // console.log('clients', response.data.data.shipments)
          const dt = response.data.data.shipments
          const data = []
          for (let i = 0; i < dt.length; i++) {
            const userData = {
              shipping_mark: {
                name: dt[i].shipping_mark,
                id: dt[i].id,
              },
              phone: dt[i].phone,
              ctn: dt[i].ctn,
              cbm: dt[i].cbn,
              usd: dt[i].total_usd,
              naira: dt[i].total_naira,
              container: dt[i].container_no,
              id: dt[i].id,
              key: dt[i].id,
              action: {
                name: 'Delete',
                url: dt[i].id,
              },
            }
            data.push(userData)
          }
          // console.log('------- ',data)
          this.usersData = data
        })
        .catch(error => {
          // console.log(error)
          this.$notification['warning']({
            message: 'Shipment Data',
            description: 'Sorry an error occurred',
          })
          return error
        })
    },
  },
}
</script>

